  <script>
  import axios from 'axios';
  
  export default {
    name: 'App',
  
    data() {
      return {
        jobPostingList: '',
  
        //공용
        pages: '',   //pageVO
        pageList: '',  //pageVO.pageList 배열값
        detailNum: '',
  
        //페이지이동에 필요한 초기값들
        page: 1,
        amount: 3,
        prev: '',
        pageStart: '',
        pageEnd: '',
        realEnd: '',
  
        total: '',
  
        //게시글 리스트
        list: [],
      }
    },
    methods: {
      getTop() {
        axios.get("/main2/?amount=" + this.amount + "&page=" + this.page
        ).then(res => {
          console.log(res)
          console.log(res.data)
          this.jobPostingList = res.data;
  
          console.log(res.data.pageVO)
          this.pages = res.data.pageVO;
          this.pageList = this.pages.pageList;
          this.total = res.data.pageVO.total;
          //페이지이동에 필요한 데이터 담기
          this.page = this.pages.page;
          this.searchTitle = this.pages.cri.searchTitle;
          this.searchContent = this.pages.cri.searchContent;
          this.prev = this.pages.prev;
          this.pageStart = this.pages.pageStart;
          this.pageEnd = this.pages.pageEnd;
          this.realEnd = this.pages.realEnd;
        }).catch(err => {
          console.log(err)
          console.log(err.data)
        })
  
  
      },
      getBottom() {
        axios.get("/main2"
        ).then(res => {
          console.log(res)
          console.log(res.data)
          this.jobPostingList = res.data;
  
        }).catch(err => {
          console.log(err)
        })
  
  
      },
  
      getDetail(j_no) {
        this.$router.push({
          path: '/jobPostingDetail',
          name: 'jobPostingDetail',
          params: { 'j_no': j_no }
        })
      },
  
      goBeforePage() {
        if (this.page > 1) {
          this.page = this.page - 1;
          this.getTop();
        } else {
          alert("첫번째 페이지입니다.");
        }
      },
      thisPage(target) {
        this.page = target.innerHTML;
        this.getTop();
      },
      goNextPage() {
        if (this.page < this.realEnd) {
          this.page = this.page + 1;
          this.getTop();
        } else {
          alert("마지막 페이지입니다.");
        }
      },
    },
  
    created(){
        this.getTop()
    },

    mounted() {
        this.getBottom()
    },
  
  
  };
  </script>
   
